import React, { PureComponent } from 'react'

export class Button extends PureComponent {

  render(props) {
    return (
      <>
        <button>
            <a href={this.props.url} rel="noreferrer" target='_blank'>
                <p>  
                    {this.props.name} 
                </p>
            </a>
        </button>
      </>
    )
  }
}

export default Button