import React, { PureComponent } from 'react'
import Button from './Button'

export class Programs extends PureComponent {

  render() {
    return (
      <>
        <div id='programs'>
           
            <div id='programsBtn'>
                <Button name = 'Konwerter' />
                <Button name = 'Konwerter 2' />
                <Button name = 'SCSS lib.' />
                <Button name = "Atlas '85" />
            </div>

            <div id='programsDescription'>
                <p>
                   Programy użytkowe.
                </p>
            </div>
        </div>        
      </>
    )
  }
}

export default Programs